<template>
   
      <div class="wrapper">
        <div class="title">
            我的订单
        </div>
        <div class="orders">
            <div class="order" 
            v-for="(item,index) in list"
            :key="index">
                <div class="order__title">
                  {{item.shopname}}
                    <span class="order__status">
                        {{item.iscanceled ?'已取消':'已下单'}}
                    </span>
                </div>
                <div class="order__content">
                    <div class="order__content__imgs">
                        <template  v-for="(inneritem,innerindex) in item.products"
                        :key="innerindex">
                        <img class="order__content__img" 
                        :src="inneritem.product.img" >
                    </template>
                    </div>
                    <div class="order__content__info">
                        <div class="order__content__info__price">{{item. totalprice}}</div>
                        <div class="order__content__info__count">共{{item.totalnumber}}件</div>
                    </div>
                </div>
            </div>
        </div>
      </div>
      <docker :currentindex="2"/>
  
</template>


<script>
import{reactive,toRefs} from 'vue'
   import docker from '../../components/docker.vue'
   import { get } from '../../utils/request'
   const useorderlisteffect = ()=>{
    const data = reactive({list:[]})
    const getnearbylist = async()=>{
    const result = await get('/api/order')
   
    if(result?.errno ===0 && result?.data?.length){
        const orderlist = result.data
        orderlist.forEach((order) => {
            const products = order.products || []
            let totalprice =0 
            let totalnumber =0

            products.forEach((productitem)=>{
                totalnumber += (productitem?.orderSales || 0)
                totalprice += ((productitem?.product?.price * productitem?.orderSales)|| 0)
            })
            order. totalprice =  totalprice
            order.totalnumber = totalnumber
        });
    data.list = result.data;
  }
   }
   const {list} = toRefs(data)
   getnearbylist()
    return {list}
   }
   export default {
        name:'orderlist',
        components:{docker},
        setup() {
           const {list} =  useorderlisteffect()
           console.log(list)
            return { list}
        }
    }
</script>

<style lang="scss" scoped>
   
    .wrapper{
      overflow:auto;
      position: absolute;
      left: 0;
      top: 0;
      bottom: .5rem;
      right: 0;
      background: rgba(248,248,248);
    }
    .title{
        font-size: 0.16rem;
        background: #fff;
        color: #333;
        line-height: 0.44rem;
        text-align: center;
    }
   
    .order{
        padding: .16rem;
        background: #fff;
        margin-top: .16rem .18rem;
        &__title{
            margin-bottom: .16rem;
            line-height: .22rem;
            font-size: 0.16rem;
            color: #333;
        }
        &__status{
            font-size: .14rem;
            color: #999;
            float: right;
        }
        &__content{
            display: flex;
            &__imgs{
                flex: 1;
            }
            &__img{
                width: 0.4rem;
                height: 0.4rem;
                margin-right: .12rem;
            }
            &__info{
                width: 0.7rem;
            
            &__price{
                margin-bottom: .04rem;
                line-height: 0.2rem;
                font-size: .14rem;
                color: #e93b3b;
                text-align: right;
            }
            &__count{
                line-height: .14rem;
                font-size: .12rem;
                color: #333;
                text-align: right;
            }
            }
        }
    }
  
  
  </style>
  